import Swiper from 'swiper';
import { Keyboard, Navigation, Pagination } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { BASE } from './config';

const aboutBtnForward = document.querySelector('.about-swiper-button-next');
const aboutBtnBack = document.querySelector('.about-swiper-button-prev');

const data = [
  {
    id: 1,
    description:
      '–í—Å–µ –ø–æ—á–∞–ª–æ—Å—è —É 2015 —Ä–æ—Ü—ñ –∑ –∫—ñ–ª—å–∫–æ—Ö –Ω–µ–±–∞–π–¥—É–∂–∏—Ö –ª—é–¥–µ–π —Ç–∞ –æ–¥–Ω–æ–≥–æ –≤—Ä—è—Ç–æ–≤–∞–Ω–æ–≥–æ —Å–æ–±–∞–∫–∏. –°—å–æ–≥–æ–¥–Ω—ñ –º–∏ ‚Äî –æ–¥–∏–Ω –∑ –Ω–∞–π–±—ñ–ª—å—à–∏—Ö –ø—Ä–∏—Ç—É–ª–∫—ñ–≤ —É —Ä–µ–≥—ñ–æ–Ω—ñ, –∞–ª–µ –Ω–∞—à–∞ –º–µ—Ç–∞ –Ω–µ–∑–º—ñ–Ω–Ω–∞: –¥–∞—Ç–∏ –¥—Ä—É–≥–∏–π —à–∞–Ω—Å —Ç–∏–º, –∫–æ–≥–æ –∑—Ä–∞–¥–∏–ª–∏.',
  },
  {
    id: 2,
    description:
      '–ú–∏ —Ä—è—Ç—É—î–º–æ, —Ä–µ–∞–±—ñ–ª—ñ—Ç—É—î–º–æ —Ç–∞ –∑–Ω–∞—Ö–æ–¥–∏–º–æ –ª—é–±–ª—è—á—ñ —Ä–æ–¥–∏–Ω–∏ –¥–ª—è –±–µ–∑–ø—Ä–∏—Ç—É–ª—å–Ω–∏—Ö —Ç–≤–∞—Ä–∏–Ω. –ù–∞—à–∞ –º–µ—Ç–∞ ‚Äî –Ω–µ –ø—Ä–æ—Å—Ç–æ –¥–∞—Ç–∏ –ø—Ä–∏—Ö–∏—Å—Ç–æ–∫, –∞ –π –∑–∞–±–µ–∑–ø–µ—á–∏—Ç–∏ –∫–æ–∂–Ω–æ–º—É "—Ö–≤–æ—Å—Ç–∏–∫—É" —â–∞—Å–ª–∏–≤–µ —Ç–∞ –ø–æ–≤–Ω–æ—Ü—ñ–Ω–Ω–µ –∂–∏—Ç—Ç—è –≤ –Ω–æ–≤—ñ–π —Ä–æ–¥–∏–Ω—ñ.',
  },
  {
    id: 3,
    description:
      '"–•–∞—Ç–∏–Ω–∫–∞ –õ–∞–ø–æ–∫" ‚Äî —Ü–µ –∫–æ–º–∞–Ω–¥–∞ –ø—Ä–æ—Ñ–µ—Å—ñ–π–Ω–∏—Ö –≤–µ—Ç–µ—Ä–∏–Ω–∞—Ä—ñ–≤, –∫—ñ–Ω–æ–ª–æ–≥—ñ–≤ —Ç–∞ –¥–µ—Å—è—Ç–∫—ñ–≤ –≤–æ–ª–æ–Ω—Ç–µ—Ä—ñ–≤, —è–∫—ñ —â–æ–¥–Ω—è –≤–∫–ª–∞–¥–∞—é—Ç—å —Å–≤–æ—é –¥—É—à—É —Ç–∞ —á–∞—Å —É —Ç—É—Ä–±–æ—Ç—É –ø—Ä–æ –Ω–∞—à–∏—Ö –ø—ñ–¥–æ–ø—ñ—á–Ω–∏—Ö. –ú–∏ –ø—Ä–∞—Ü—é—î–º–æ 24/7, –±–æ —ó—Ö–Ω—î –∂–∏—Ç—Ç—è –∑–∞–ª–µ–∂–∏—Ç—å –≤—ñ–¥ –Ω–∞—Å.',
  },
  {
    id: 4,
    description:
      '–ú–∏ —Å—Ç–≤–æ—Ä–∏–ª–∏ –±–µ–∑–ø–µ—á–Ω–∏–π —Ç–∞ –∫–æ–º—Ñ–æ—Ä—Ç–Ω–∏–π –ø—Ä–æ—Å—Ç—ñ—Ä. –ö–æ–∂–Ω–∞ —Ç–≤–∞—Ä–∏–Ω–∞ –æ—Ç—Ä–∏–º—É—î —è–∫—ñ—Å–Ω–µ —Ö–∞—Ä—á—É–≤–∞–Ω–Ω—è, —Å–≤–æ—î—á–∞—Å–Ω—É –≤–µ—Ç–µ—Ä–∏–Ω–∞—Ä–Ω—É –¥–æ–ø–æ–º–æ–≥—É, –ø—Ä–æ—Ö–æ–¥–∏—Ç—å —Å–æ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—é —Ç–∞ –≥—É–ª—è—î –Ω–∞ —Å–ø–µ—Ü—ñ–∞–ª—å–Ω–æ –æ–±–ª–∞–¥–Ω–∞–Ω–∏—Ö –º–∞–π–¥–∞–Ω—á–∏–∫–∞—Ö.',
  },
  {
    id: 5,
    description:
      '–í–∞—à–∞ –¥–æ–ø–æ–º–æ–≥–∞ ‚Äî –±–µ–∑—Ü—ñ–Ω–Ω–∞. –í–∏ –º–æ–∂–µ—Ç–µ –≤–∑—è—Ç–∏ —Ç–≤–∞—Ä–∏–Ω–∫—É –¥–æ–¥–æ–º—É, —Å—Ç–∞—Ç–∏ –≤–æ–ª–æ–Ω—Ç–µ—Ä–æ–º, –¥–æ–ø–æ–º–æ–≥—Ç–∏ —Ñ—ñ–Ω–∞–Ω—Å–æ–≤–æ –∞–±–æ —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ–π–Ω–æ. –ö–æ–∂–µ–Ω –º–∞–ª–µ–Ω—å–∫–∏–π –≤–Ω–µ—Å–æ–∫ –Ω–∞–±–ª–∏–∂–∞—î –Ω–∞—Å –¥–æ –≤–µ–ª–∏–∫–æ—ó –º–µ—Ç–∏ ‚Äî —Å–≤—ñ—Ç—É –±–µ–∑ –±–µ–∑–ø—Ä–∏—Ç—É–ª—å–Ω–∏—Ö —Ç–≤–∞—Ä–∏–Ω.',
  },
];

const aboutRender = data.map(({ id, description }) => {
  return `<div class="swiper-slide about-slide">
          <picture>
            <source
              media="(min-width: 1440px)"
              srcset="${BASE}about-us/desktop/slide-p${id}-desk.webp 1x, ${BASE}about-us/desktop/slide-p${id}-desk@2x.webp 2x">
            <source
              media="(min-width: 768px)"
              srcset="${BASE}about-us/tablet/slide-p${id}-tab.webp 1x, ${BASE}about-us/tablet/slide-p${id}-tab@2x.webp 2x">
            <source
              media="(max-width: 767px)"
              srcset="${BASE}about-us/mobile/slide-p${id}-mob.webp 1x, ${BASE}about-us/mobile/slide-p${id}-mob@2x.webp 2x">
            <img 
              class="about-picture"
              src="${BASE}about-us/mobile/slide-p${id}-mob.webp"
              alt="slide" loading="lazy" decoding="async"/>
          </picture>
          <div class="about-overlay">
          <p class="about-id">${description}</p>
          </div>
        </div>`;
}).join('');

document.querySelector('.about-swiper-wrapper').innerHTML = aboutRender;

const swiper = new Swiper('.about-mySwiper', {
  modules: [Navigation, Pagination, Keyboard],
  loop: false,
  slidesPerView: 1,
  spaceBetween: 10,

  keyboard: {
    enabled: true,
  },

  pagination: {
    el: '.about .swiper-pagination',
    clickable: true,
  },

  navigation: {
    nextEl: '.about-swiper-button-next',
    prevEl: '.about-swiper-button-prev',
  },
});

function updatePaginationPosition() {
  const paginationEl = document.querySelector('.about .swiper-pagination');
  if (window.innerWidth < 768) {
    paginationEl.classList.remove('center');
    paginationEl.classList.add('left');
  } else {
    paginationEl.classList.remove('left');
    paginationEl.classList.add('center');
  }
}

updatePaginationPosition();
window.addEventListener('resize', updatePaginationPosition);

const updateNavigationState = () => {
  if (swiper.isEnd) {
    aboutBtnForward.classList.add('about-btn-disabled');
  } else {
    aboutBtnForward.classList.remove('about-btn-disabled');
  }
  if (swiper.isBeginning) {
    aboutBtnBack.classList.add('about-btn-disabled');
  } else {
    aboutBtnBack.classList.remove('about-btn-disabled');
  }
};
swiper.on('slideChange', updateNavigationState);
updateNavigationState();

const aboutSection = document.querySelector('.about');

swiper.on('slideChangeTransitionStart', () => {
  if (!aboutSection) return;
  document
    .querySelectorAll('.about-id')
    .forEach(el => el.classList.remove('show'));
  document
    .querySelectorAll('.about-overlay')
    .forEach(el => el.classList.remove('fade-out'));
});

swiper.on('slideChangeTransitionEnd', () => {
  const activeSlide = swiper.slides[swiper.activeIndex];

  const text = activeSlide.querySelector('.about-id');
  if (text) text.classList.add('show');

  const overlay = activeSlide.querySelector('.about-overlay');
  if (overlay) overlay.classList.add('fade-out');
});

const observer = new IntersectionObserver(
  entries => {
    entries.forEach(entry => {
      if (entry.isIntersecting) {
        const activeSlide = swiper.slides[swiper.activeIndex];
        const text = activeSlide.querySelector('.about-id');
        const overlay = activeSlide.querySelector('.about-overlay');

        if (text) {
          text.classList.remove('show');
          void text.offsetWidth;
          text.classList.add('show');
        }

        if (overlay) {
          overlay.classList.remove('fade-out');
          void overlay.offsetWidth;
          overlay.classList.add('fade-out');
        }
      }
    });
  },
  { threshold: 0.7 }
);

observer.observe(aboutSection);
